/*
  GRID
*/

.row {
  @include clearfix;

  margin: 20px;
}
.flex-row {
  @include flex;
}
.flex-col1of2 {
  width: calc(50% - 10px);
  margin-left: 20px;

  &:first-child {
    margin-left: 0;
  }
}
.span2of12 {
  float: left;
  margin-right: gutter();
  width: span(2 of 12);

  @include phone {
    float: none;
    width: 100%;
    margin-left: 0;
  }

  &.last {
    margin-right: 0;
    @include phone {
      float: none;
      width: 100%;
      margin-left: 0;
    }
  }
}
