/*
  SECTION HOME
*/

section, footer {
  padding: 104px 0;

  &.scale {
    @include respond-to(1130px) {
      @include transform(translateX(-240px) scale(.6));
      height: 100vh;
      top: -20vh;
      left: -10vw
    }

    @include phone {
      top: -15vh;
      left: -10vw;
    }

    @include phoneSmall {
      left: 5%;
    }
  }

  .section-title {
    color: $primaryNavy;
    font-family: $avenirNext;
    font-size: 3.2rem;
    font-weight: 400;
    margin: auto;
    max-width: 700px;
    text-align: center;

    @include phone {
      font-size: 2rem;
      transform: none;
    }
  }
}

.section-home {
  overflow: hidden;
  padding: 60px 0 120px;
  position: relative;

  @include phone {
    padding: 115px 0 60px;
  }

  .container {
    @include respond-to(1000px) {
      padding-left: 40px;
    }
    @include phone {
      padding-left: 40px;
    }
  }

  .btn-light-blue {
    text-align: center;
    width: 160px;
    margin-right: 20px;

    @include tablet {
      margin-right: 10px;
      padding-left: 0;
      padding-right: 0;
    }

    @include phone {
      @include transform(translateX(-50%));
      left: 50%;
      margin-right: 100%;
      padding-left: 15px;
      padding-right: 15px;
      position: relative;
    }
  }

  .btn-transparent {
    text-align: center;
    padding-left: 30px;
    margin-right: 10px;
    width: 160px;

    .play-button {
      position: relative;
  
      @include tablet {
        padding-left: 15px;
      }
  
      @include phone {
        padding-left: 0;
      }
  
      &:before {
        @include border-radius(100%);
        border: 1px solid $layoutWhite;
        width: 24px;
        height: 24px;
        content: "";
        position: absolute;
        left: 0;
        top: -4px;
      }
      &:after {
        position: absolute;
        background: transparent;
        width: 3px;
        height: 1px;
        content: "";
        border-style: solid;
        border-color: transparent transparent transparent $layoutWhite;
        border-width: 5px 0 5px 10px;
        top: 4px;
        left: 9px;
      }
    }

    &.btn-modal {
      @include tablet {
        margin-right: 0;
        padding-left: 15px;
        padding-right: 0;
      }

      @include phone {
        padding-left: 30px;
      }
    }

    @include phone {
      @include transform(translateX(-50%));
      left: 50%;
      position: relative;
    }
  }

  &:before {
    background-image: url(../images/bg-home.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center bottom;
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    z-index: -2;

    @include phone {
      height: 85%;
      margin-left: -50%;
      width: 200%;
    }

  }

  .home-title {
    font-family: $cohedline;
    color: $layoutWhite;
    margin: 40px 0 80px;
    font-size: 4rem;
    line-height: 1.6;
    position: relative;

    .light {
      display: inline-block;
      font-weight: 100;
    }

    @include phone {
      font-size: 4rem;
      margin: 0 0 40px;
      text-align: center;
    }
  }
  .rate-calculator {
    position: relative;

    @include phone {
      max-width: 400px;
      margin: 40px auto 0;
    }
    p {
      color: $layoutMediumBlack;
    }

    .current-rate {
      @include border-radius(100%);
      @include box-shadow(0 0 30px 0px rgba(0,0,0,0.1));
      background-color: $layoutWhite;
      width: 150px;
      height: 150px;
      margin: 0 0 30px auto;
      right: 15px;
      text-align: center;
      color: $layoutMediumBlack;
      position: absolute;
      z-index: 3;

      @include phone {
        height: 110px;
        right: 65%;
        width: 110px;
      }

      .lbl {
        display: block;
        padding: 40px 0 8px;

        @include phone {
          font-size: 1.2rem;
          padding: 24px 0 0;
        }
      }

      .rate-value {
        font-size: 3.1rem;
        font-family: $cohedline;
        font-weight: 700;
        margin: 0 0 10px;

        @include phone {
          font-size: 2.4rem;
          margin-bottom: 5px;
        }
      }
    }

    .layout1 {
      position: absolute;
      top: 60px;
      left: 0;
      z-index: 2;

      @include tablet {
        @include transform(translateX(-50%) scale(1.1));
        left: 85%;
        position: relative;
        top: 105px;
      }

      @include phone {
        @include transform(translateX(-50%) scale(1.1));
        left: 80%;
        position: relative;
        top: 45px;
      }

      @include phoneSmall {
        left: 70%;
      }

      img {
        width: 207px;

        @include tablet {
          width: 235px;
        }
        @include phone {
          width: 207px;
        }
      }
    }

    .layout2 {
      position: relative;
      top: 130px;
      right: 40px;
      z-index: 1;

      .play-button {
        z-index: 2;
        top: 175px;
        left: 358px;
        width: 80px;
        height: 80px;
        position: absolute;
        background-image: url('/static/img/play.svg');
        background-size: cover;

        &:hover {
          border-radius: 50%;
          box-shadow: 0px 0 25px 10px rgba(193,193,193,0.25);
        }

        &::after, &::before {
          display: none;
        }
      }

      @include tablet {
        display: none;
      }

      .non-retina {
        display: block;

        @include respond-from(1600px) {
          display: none;
        }
      }
      .retina {
        display: none;

        @include respond-from(1600px) {
          z-index: 1;
          display: block;
          width: 796px;
        }
      }
    }

    .submit-wrap {
      height: 0;
      text-align: center;

      .btn-blue {
        padding-left: 30px;
        padding-right: 30px;
      }
    }
  }
  p {
    color: rgba($layoutMediumBlack, 0.6);

    @include phone {
      color: $layoutWhite;
    }
    a {
      font-weight: 700;
      color: $layoutMediumBlack;

      @include phone {
        color: $layoutWhite;
      }
    }
  }
}
.styled-list {
  color: $layoutWhite;
  margin: 0 0 100px;
  padding: 0;

  @include phone {
    display: none;
  }

  li {
    list-style: none;
    padding-left: 30px;
    font-size: 1.8rem;
    margin-bottom: 15px;
    position: relative;

    &:before {
      @include border-radius(100%);
      border: 1px solid $layoutWhite;
      width: 14px;
      height: 14px;
      content: "";
      position: absolute;
      left: 0;
      top: 3px;
    }
    &:after {
      @include border-radius(100%);
      background-color: $layoutWhite;
      width:8px;
      height: 8px;
      content: "";
      position: absolute;
      left: 4px;
      top: 7px;
    }
  }
  &.right-arrow {
    li {
      &:before {
        color: $layoutWhite;
        border: none;
      }
      &:after {
        @include border-radius(0);
        @include transform(rotate(315deg));
        background: transparent;
        width: 6px;
        height: 6px;
        border-right: 1px solid $layoutWhite;
        border-bottom: 1px solid $layoutWhite;
        top: 8px;
        left: 6px;
      }
    }
  }
  &.check {
    color: $layoutMediumBlack;
    li {
      &:before {
        border-color: $supportGreen;
      }
      &:after {
        @include border-radius(0);
        @include transform(rotate(45deg));
        background: transparent;
        border-right: 2px solid $supportGreen;
        border-bottom: 2px solid $supportGreen;
        width: 4px;
        height: 8px;
        top: 6px;
        left: 6px;
      }
    }
  }
}
.modal-rate {
  .title {
    font-size: 3.2rem;
    font-family: $cohedline;
    text-align: center;
  }
  .percent {
    font-size: 5rem;
    color: $layoutMediumBlack;
    font-weight: 700;
    text-align: center;
  }
}
.modal-video {
  video {
    position: relative;
    top: 0;
    max-width: 1080px;
    width: 90vw;
  }
  .video-overlay {
    display: none;
    background-color: #000;
    bottom: 0;
    height: 100%;
    max-width: 1080px;
    opacity: 0.5;
    position: absolute;
    left: 0;
    width: 90vw;
  }

  .controls {
    @include transition( all 0.5s);
    bottom: 2vh;
    background-color: transparent;
    display: flex;
    left: 0;
    height: 20px;
    margin-left: 5px;
    max-width: 1080px;
    padding-left: 0;
    position: absolute;
    opacity: 0;
    width: 90vw;

    input[type="range"] {
      -webkit-appearance: none;
      -moz-apperance: none;
      -o-apperance: none;
      background: $layoutWhite;
      border-radius: 10px;
      height: 5px;
      margin-right: 10px;
      margin-top: 10px;
      outline: none;
      padding: 0;

      &.progress-bar {
        width: 70%;
      }
      &.volume-bar {
        width: 15%;
      }
    }
    input[type="range"]::-webkit-slider-thumb {
      @include border-radius(20px);
      -webkit-appearance: none;
      -moz-apperance: none;
      -o-apperance: none;
      background-color: $primaryMagenta;
      border-radius: 20px;
      border: 2px solid $layoutWhite;
      height: 15px;
      width: 15px;
    }

    button {
      background-color: transparent;
      border: none;
      display: inline-block;
      margin-right: 10px;
      transform: scale(.8);

      &:hover {
        @include border-radius(10%);
        background-color: $primaryMagenta;
        height: 30px;
      }
      img {
        height: 18px;
        margin-top: 5px;
        padding-bottom: 15px;
        width: 18px;

        &.mute {
          display: block;
        }
        &.unmute {
          display: none;
        }
        &.pause {
          display: block;
        }
        &.play {
          display: none;
        }
      }
      .full-screen-btn {
        margin-right: 0;
      }
    }
  }
  &:hover {
    .controls {
      @include transition( all 0.5s);
      opacity: 1;
    }
  }
}
.icon-q {
  @include border-radius(100%);
  margin: auto;
  text-align: center;
  font-size: 1.2rem;
  text-decoration: none;
  line-height: 20px;
  width: 20px;
  height: 20px;
  display: block;
  border: 1px solid rgba($layoutMediumBlack, 0.3);
  color: rgba($layoutMediumBlack, 0.4);
}
