/*
  FORM
*/

@import
"input",
"checkbox",
"slider-checkbox",
"select",
"address",
"radio",
"stripe";

.form {
  label {
    font-size: 1.4rem;
    margin-bottom: 10px;
    display: block;
  }
}
.form-l {
  label {
    display: block;
    font-size: 1.8rem;
    margin-bottom: 60px;
  }
}
label {
  &.hidden {
    visibility: hidden;
    z-index: -1;
    position: absolute;
  }
}
.form-marketing {
  label {
    display: block;
    font-size: 1.4rem;
    color: rgba($layoutMediumBlack, 0.6);
    margin-bottom: 10px;
  }
  .input-wrap {
    margin-bottom: 20px;
  }
  &.light {
    label {
      color: rgba($layoutWhite, 0.6);
    }
  }
}
.submit-wrap {
  margin-top: 60px;
}
.error-msg {
  color: $supportRed;
  position: absolute;
  top: 100%;
  font-size: 1.4rem;
  left: 0;

  &.static {
    margin: 10px 0;
    position: relative;
  }
}
.error-msg-static {
  @include border-radius(6px);
  background-color: rgba(0,0,0,0.15);
  color: $supportRed;
  font-size: 1.4rem;
  font-weight: 400;
  margin: 10px 0;
  padding: 12px 12px 12px 18px;
  position: relative;
  position: relative;

  &:before {
    @include border-radius(6px 0 0 6px);
    content: "";
    background-color: $supportRed;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 8px;
  }
}
.form-error {
  color: $supportRed;
}
.input-wrap {
  position: relative;
}
