/*
	GLOBAL
*/

html {
	font-size: 62.5%;
	height: 100%;
	min-height: 100%;
}

html, body {
	position: relative;
}

body {
	@include transition( all 0.5s);
	font-family: $opensans;
	font-size: 1.4rem;
	font-weight: 100;
	margin: -1px 0;
	min-height: 100%;
	overflow-x: hidden;
	padding: 1px 0;
	left: 0;

	&.page-investment {
		height: 100%;
		background-attachment: fixed;
		background-image: url(../img/bg/bg.jpg);
		background-position: center center;
		background-repeat: no-repeat;
		-webkit-background-size: cover;
		-moz-background-size: cover;
		-o-background-size: cover;
		background-size: cover;

		@include phone {
			background-repeat: repeat;
			background-attachment: scroll;
		}
	}
	.highlight {
		font-weight: 700;
		padding-bottom: 16px;

		&:hover {
			color: $primaryBlue;
			span {
				color: $primaryBlue;
			}
		}

		@include respond-to(1130px) {
			border: none;
			font-weight: 500;
			padding-bottom: 0;
		}
	}
	.main-header {
		.site-nav {
			ul {
				&.menu-list {
					@include transform(translateX(-50%));
					display: none;
					color: $primaryNavy;
					position: absolute;
					z-index: 9999;
					left: 200px;
					height: 200px;
					top: 60px;
					margin: auto;
					background: $layoutWhite;
					width: 600px;
					padding: 50px;

					@include respond-to(1130px) {
		        height: auto;
						top: 0;
		      }

					&.show {
						display: block;

						li {
							a {
								color: $primaryNavy;
							}
						}

						@include respond-to(1130px) {
							position: relative;
							background: transparent;
							border: none;
							box-shadow: none;
							left: 49%;
							padding: 0;
							padding-left: 10px;
							width: auto;

							li {
								margin: 10px 0;
								padding-bottom: 10px;
								a {
									@include respond-to(1130px) {
			              color: $primaryNavy;
			              font-size: 1.4rem;
			              margin-left: 0;
			            }
								}
							}

						}
					}
					li {
						display: block;
						padding-bottom: 20px;
						cursor: pointer;
						&:last-child {
							padding-bottom: 0;
						}
						a {
							color: $primaryNavy;
							padding: 0;
						}
					}
				}
				.description {
					border-right: solid 1px $layoutGrey;
          display: inline-table;
					height: 200px;
					padding-right: 50px;
					width: 40%;

					@include respond-to(1130px) {
		        display: none;
		      }

					h3 {
						font-family: $cohedline;
						font-weight: 400;
						font-size: 2.4rem;
						margin-top: 0;
					}

					p {
						font-family: $avenirNext;
						font-size: 1.8rem;
					}
					a{
						&.link {
	            color: $primaryBlue;
	    				display: inline-block;
	            font-size: 1.6rem;
	    				font-weight: 500;
							padding-right: 8px;
	    				position: relative;

	    				img {
	    					position: absolute;
	    					top: 0;
	    				}
	    				&:hover {
	              color: $primaryBlue;
	    					border-bottom: 1px solid $primaryBlue;
	    				}
	    			}
					}
        }
				.menu-items {
					display: inline-table;
					width: 50%;

					li {
						display: inline-flex;
						margin-left: 50px;
						width: 100%;

						@include respond-to(1130px) {
			        display: block;
			      }

						img {
							@include respond-to(1130px) {
								display: none;
							}
						}

						a {
							font-size: 1.6rem;
							margin-top: 3px;
							&:hover {
								color: $primaryNavy;
							}
							span {
								&:hover {
									color: $primaryNavy;
								}
							}
						}
					}
					.square {
						height: 20px;
						width: 20px;
						margin-right: 20px;
					}
				}
				li {
					margin-right: 15px;
				}
			}
		}
	}

	.top-banner {
		background-color: $layoutLightGrey;
		height: 40px;
		font-size: 1.4rem;
		width: 100vw;

		@include menuBreak {
			display: none;
		}

		a {
			color: $layoutMediumBlack;
			text-decoration: none;
			padding-right: 10px;
			padding-bottom: 6px;

			&.sign {
				color: $primaryBlue;
				font-weight: 400;
				img {
					margin-right: 8px;
				}
			}
		}
		.left-nav {
			float: left;
			padding: 10px 0;
		}
		.right-nav {
			float: right;
			padding: 10px 0;
		}
	}

	&.no-bg {
		background: $layoutWhite;
	}
	p {
		font-size: 1.4rem;
	}
	&.fixed {
		position: fixed;
		overflow: hidden;
	}
}
.container {
	margin: auto;
	max-width: 1180px;
	padding: 0 20px;

	.page-investment & {
		max-width: 1000px;
	}

	@include clearfix;
}
.main {
	margin-left: 260px;
	padding-top: 120px;

	&.has-msg {
		padding-top: 230px;
	}

	@include tablet {
		margin-left: 0;
		padding-top: 100px;
	}
	.has-bar-switch & {
		padding-top: 170px;
	}

	&.footer {
		margin-top: 40px;
		margin-bottom: 40px;

		p {
			margin: 10px 10px 20px;
			color: rgba($layoutWhite, 0.5);
		}
	}
}
pre {
	background-color: transparent;
	font-family: $opensans;
	font-weight: 200;
	white-space: pre-wrap;
	width: 100%;
	overflow: hidden;
}
.box {
	@include border-radius(4px);
	background: rgba($primaryNavy, 0.4);

	&.white {
		background-color: $layoutWhite;
	}
}
.box-referral {
	@include border-radius(4px);
	background: rgba($primaryNavy, 0.4);
	padding: 20px;

	label {
		margin: 0 !important;
		text-decoration: underline;
		cursor: pointer;
	}

	input {
		margin-top: 10px;
		border: rgba($primaryNavy, 0.4);
	}
}
.txt-c {
	text-align: center;
}
.txt-l {
	text-align: left;
}
.txt-r {
	text-align: right;
}
.float-l {
	float: left;
}
.float-r {
	float: right;
}
.p10 {
	padding: 10px;
}
.p20 {
	padding: 20px;
}
.p40 {
	padding: 40px;
}
.pb60 {
	padding-bottom: 60px;
}
.mb {
	margin-bottom: 20px;
}
.mb0 {
	margin-bottom: 0px;
}
.mb10 {
	margin-bottom: 10px;
}
.mb40 {
	margin-bottom: 40px;
}
.title {
	font-weight: 400;
}
.box {
	background: $bgBox;
}
.mt20 {
	margin-top: 20px;
}
.mt40 {
	margin-top: 40px;
}
.mt60 {
	margin-top: 60px;
}
.mt100 {
	margin-top: 100px;
}
.mb20 {
	margin-bottom: 20px;
}
.inline-block {
	display: inline-block;
}
.rel {
	position: relative;
}

section {

	color: $primaryNavy;

	h1, h2, h3 {
		font-family: $cohedline;
		font-weight: 400;
	}
	h1 {
		font-size: 4rem;
		line-height: 48px;
	}
	h2 {
		font-size: 3.2rem;
		line-height: 38px;
	}
	h3 {
		font-size: 2.4rem;
		line-height: 36px;
	}
	h4, h5, p {
		font-family: $avenirNext;
	}
	h4 {
		font-size: 2rem;
		line-height: 28px;
		font-weight: 700;
	}
	h5 {
		font-size: 1.8rem;
		line-height: 26px;
		font-weight: 400;
	}
	p {
		font-size: 1.8rem;
		line-height: 24px;
		font-weight: 200;

		&.small {
			font-size: 1.4rem;
			line-height: 20px;
		}

		a {
			color: $primaryBlue;
			cursor: pointer;
			font-weight: 700;
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
			&.link {
				display: inline-block;
				font-weight: 500;
				position: relative;

				img {
					position: absolute;
					top: 5px;
				}
				&:hover {
					border-bottom: 1px solid $primaryBlue;
				}
			}
		}
	}
}

.section-cta {
	padding: 80px 0;

	&.icy-blue {
		background-color: $layoutIcyBlue;
	}
	&.grey {
		background-color: $layoutLightGrey;
	}
	&.purple {
		@include purpleGradient;
		color: $layoutWhite;

		.section-title {
			color: $layoutWhite;
		}
	}

	h2 {
		margin-bottom: 0;
	}

	span {
		color: $primaryPurple;
		font-family: $cohedline;
		font-size: 8rem;
		font-weight: 500;
	}

	.icon {
		margin: auto;
		margin-bottom: 24px;
	}
	.section-title {
		font-family: $cohedline;
	}
	.info-area {
		margin: auto;
		margin-top: 16px;
		max-width: 650px;

		&.box-elements {
			width: 100%;
		}

		p {
			margin: 0;
		}
	}
	.btn {
		margin-top: 32px;
	}
	.box-elements {
		margin-top: 32px;
	}
}

.intro {
	padding: 191px 0 104px;

	.icon {
		text-align: center;
		margin-bottom: 32px;
	}
	.warning {
		margin-top: 32px;
	}
	h1 {
		font-size: 4rem;
		font-family: $cohedline;
		font-weight: 400;
		line-height: 48px;
	}
	.content-intro {
		margin-top: 24px;
		p {
			font-size: 1.8rem;
		}
		br {
			@include tablet {
				display: none;
			}
		}
	}
	.btn {
		margin-top: 32px;
	}
	.trustpilot-widget {
		background-color: $layoutWhite;
		border-bottom: 1px solid $layoutGrey;
		padding-bottom: 100px;
		padding-top: 60px;
	}

	&.terms {
		padding: 104px 0 80px;
	}
}

.shadow-large, .shadow-small {
  @include border-radius(4px);
  @include box-shadow(0 0 24px 0 rgba(0, 0, 0, .1));
}
.shadow-large {
	height: 80px;
	width: 80px;
}
.shadow-small {
	height: 50px;
	width: 50px;
}
.block {
	@include border-radius(4px);
	background-color: $layoutWhite;

	&.raised {
		@include box-shadow(0 8px 24px 0 rgba($layoutBlack, 0.1));
	}

	&.raised-small {
		@include box-shadow(0 2px 8px 0 rgba($layoutBlack, 0.1));
	}
	&.raised-large {
		@include box-shadow(0 8px 32px 0 rgba($layoutBlack, 0.1));
	}
}

.cookie-preferences {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;

	@include respond-to(950px) {
		bottom: 60px;
	}
	@include respond-to(560px) {
		bottom: 79px;
	}
	.css-58qk62-Root {
		background: $layoutWhite;
		color: $primaryNavy;
		opacity: 0.9;
	}
}
.css-yui1fz-InputCell {
	width: 65px;
}
.css-gt6w9i-CloseButton {
	&.ellcril3 {
		font-weight: 700;
		top: 18px;
	}
}
