/*
  INPUT
*/

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input {
  border: 1px solid $layoutWhite;
  background: transparent;
  width: calc(100% - 42px);

  &[type="text"], &[type="number"] {
    .form-l & {
      font-family: $cohedline;
      font-size: 4rem;
      line-height: 40px;
      padding: 30px 20px;
    }
    .form-marketing & {
      border: 1px solid rgba(0,0,0,.1);
      padding: 20px;
      width: calc(100% - 42px);
      font-size: 1.4rem;

      &:focus {
        outline: none;
        border: 1px solid rgba($layoutMediumBlack, 0.6);
      }
    }
    .form-marketing.light & {
      color: rgba($layoutWhite, 0.6);
    }
    &.error {
      border-color: $supportRed;
    }

  }
}
textarea {
  border: 1px solid rgba(0,0,0,.1);
  padding: 20px;
  background: transparent;
  width: calc(100% - 42px);

  &:focus {
    outline: none;
    border: 1px solid rgba($layoutMediumBlack, 0.6);
  }
}
.dob {
  @include clearfix;
  input[type="number"] {
    width: calc(33.333% - 37px);
    margin-left: 15px;
    padding-left: 10px;
    padding-right: 10px;
    text-align: center;

    &:first-child {
      margin-left: 0;
    }
  }
}
.input-group {
  @include clearfix;

  .input-wrap {
    width: span(6);

    &:nth-child(2n + 2) {
      width: span(6);
    }
  }
}
.day-input {
  max-width: 80px;
}
.text-input {
  border: 1px solid rgba($layoutWhite, 0.3);
  background: transparent;
  width: calc(100% - 42px);
  padding: 14px 20px 12px;

  .form-marketing & {
    border-color: rgba($layoutMediumBlack, 0.05);
    background-color: rgba($layoutMediumBlack, 0.05);
  }
}
.input-desc {
  color: rgba($layoutMediumBlack, 0.6);
  font-size: 1.6rem;
  margin: 10px 0;
}
.input-pound {
  position: relative;

  &:before {
    background-color: rgba($layoutMediumBlack, 0.05);
    color: $primaryNavy;
    content: "£";
    font-size: 1.6rem;
    font-weight: 700;
    height: calc(100% - 2px);
    left: 0;
    top: 1px;
    line-height: 100%;
    line-height: 58px;
    position: absolute;
    text-align: center;
    width: 60px;
    z-index: 1;
  }
  input, .text-field {
    border: 1px solid $layoutLightGrey;
    padding: 20px;
    font-size: 1.4rem;

    .form-marketing & {
      width: calc(100% - 102px);
      padding-left: 80px;
    }
  }
  .text-field {
    background-color: $layoutLightGrey;
  }
}
