/*
	FONTS
*/

// AVENIR NEXT

@font-face {
  font-family: 'AvenirNext';
  src: url('../fonts/AvenirNext-Regular.woff2') format('woff2'),
       url('../fonts/AvenirNext-Regular.woff') format('woff');
  font-weight: 200;
  font-style: normal;
}

@font-face {
  font-family: 'AvenirNext';
  src: url('../fonts/AvenirNext-Regular-Italic.woff2') format('woff2'),
       url('../fonts/AvenirNext-Regular-Italic.woff') format('woff');
  font-weight: 200;
  font-style: italic;
}

@font-face {
  font-family: 'AvenirNext';
  src: url('../fonts/AvenirNext-Medium.woff2') format('woff2'),
       url('../fonts/AvenirNext-Medium.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'AvenirNext';
  src: url('../fonts/AvenirNext-DemiBold.woff2') format('woff2'),
       url('../fonts/AvenirNext-DemiBold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}

// CO HEADLINE

@font-face {
	font-family: 'Co-headline';
	src: url('../fonts/CoHeadline-Bold.eot');
	src: url('../fonts/CoHeadline-Bold.eot?#iefix') format('embedded-opentype'), url('../fonts/CoHeadline-Bold.woff') format('woff'), url('../fonts/CoHeadline-Bold.ttf') format('truetype'), url('../fonts/CoHeadline-Bold.svg') format('svg');
	font-weight: 700;
	font-style: normal;
}
@font-face {
  font-family: 'Co-headline';
  src: url('../fonts/coheadline-regular-webfont.woff2') format('woff2'),
       url('../fonts/coheadline-regular-webfont.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
