/*
  ADDRESS
*/

.address {

  .step-1 {
    .input-wrap {
      width: 200px;
      float: left;
      margin-bottom: 20px;

      @include phoneSmall {
        width: 175px;
      }
    }

    .search-again {
      display: none;
      margin: 30px 0 0 15px;
    }
    .row {
      clear: both;
      margin-bottom: 20px;

      a {
        color: rgba($layoutMediumBlack, 0.6);
        text-decoration: none;

        &:hover{
          color: $primaryMagenta;
        }
      }
    }
  }
  .step-2 {
    display: none;
  }
  .step-3 {
    display: none;
  }
}
.search-btn {
  @include border-radius(100%);
  display: inline-block;
  float: left;
  width: 60px;
  height: 60px;
  background-color: $primaryBlue;
  background-image: url(../img/icons/icon-search.svg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 20px 20px;
  margin: 30px 0 0 15px;

  &:hover {
    background-color: $primaryMagenta;
  }
}
.postcode-search {
  @include clearfix;
  input[type="text"] {
    width: calc(100% - 42px - 120px);
    float: left;

    @include phone {
      width: calc(100% - 42px);
      float: none;
    }
  }
  .btn-search {
    margin-top: 0;
    float: right;
    padding: 21px 0;
    width: 100px;

    @include phone {
      float: none;
      margin: 10px auto 0;
      padding: 15px 0;
      display: block;
    }
  }
}
.address-fields {
  .input-wrap {
    margin-bottom: 20px;
  }
  .flex-row {
    @include clearfix;
    display: block;

    @include phone {
      display: block;
    }
  }
  .flex-col1of2 {
    width: 48%;
    float: left;
    display: block;
    margin: 0 0 20px;

    @include phone {
      display: block;
      width: auto;
      margin: 0 0 20px;
    }
    &:first-child {
      margin-right: 4%;
    }
  }
}
.from-address-search {
  @include transition(all 0.3s);
  .input-wrap {
    margin-bottom: 20px;
  }
  .postcode-search {
    margin-bottom: 20px;

    .error-msg {
      margin: 0;
      padding: 10px 0 0 0;
      clear: both;
    }
  }
}


// EDIT ADDRESS form
.edit-address-form {
  .postcode-field {
    width: 170px;
    display: inline-block;
    float: left;
  }
  .postcode-btn-wrap {
    float: left;
    margin-top: 24px;

  }
  select.visible {
    padding: 14px 20px;
  }
  .postcode-search-wrap {
    @include clearfix;
    padding-bottom: 20px;
    border-bottom: 1px solid $layoutWhite;
    margin-bottom: 20px;
  }
  .postcode {
    display: inline-block;
    float: left;
    width: 300px;
  }
  .address-select {
    float: right;
    width: calc(100% - 300px);
    .selectbox {
      &:after {
        top: 16px;
      }
    }
  }
  .address-fields {
    @include clearfix;

    .input-wrap {
      width: 30%;
      margin-right: 5%;
      float: left;

      &:nth-child(3n) {
        margin-right: 0;
      }
    }
  }
  .submit-wrap {
    margin-top: 0;
    text-align: center;
  }
}
.form-signup-2 {
  .address-fields {
    @include clearfix;

    .input-wrap {
      width: 47%;
      margin-right: 6%;
      float: left;

      &:nth-child(2n) {
        margin-right: 0;
      }
    }
  }
}
