/*
  BUTTONS
*/

.btn {
  @include transition(all 0.3s);
  @include border-radius(4px);
  @include box-shadow(inset 0 0 0 2px rgba(255, 255, 255 ,0.6));
  border: none;
  color: $layoutWhite;
  cursor: pointer;
  display: inline-block;
  font-family: $avenirNext;
  font-size: 1.6rem;
  opacity: 0.5;
  padding: 14px 32px 11px;
  text-align: center;
  text-decoration: none;

  &:hover {
    opacity: 1;
  }
  &.btn-dashed {
    @include box-shadow(none);
    border: 1px dashed rgba(255, 255, 255 ,0.6);
  }
  &.normal {
    padding-left: 30px;
    padding-right: 30px;
  }
  &.loading, &.disabled {
    position: relative;
    padding-left: 40px;
    padding-right: 20px;
    opacity: 1;

    &:before {
      left: 10px;
      @include animation(loading 3s linear infinite);
      content: "";
      background-image: url(../images/icon-loader.png);
      background-size: 20px 20px;
      height: 20px;
      position: absolute;
      width: 20px;
    }
  }
  &.small {
    padding: 5px 16px;
  }
  &.large {
    padding: 18px 20px 16px;
  }
  &.get-started {
    padding: 20px 75px 18px;
  }
  &.square {
    @include border-radius(4px);
  }
  &.btn-light-gray {
    @include box-shadow(inset 0 0 0 2px rgba(0, 0, 20 ,0.2));
    color: rgba($layoutMediumBlack,0.6);
    opacity: 1;

    &:hover {
      @include box-shadow(inset 0 0 0 2px rgba(0, 0, 20 ,0.8));
      color: $layoutMediumBlack;
    }
  }
  &.btn-link {
    color: rgba($layoutMediumBlack, 0.4);

    &:hover {
      color: $layoutMediumBlack;
      text-decoration: underline;
    }
  }
  &.btn-blue {
    @include box-shadow(inset 0 0 0 2px $primaryBlue);
    background-color: $primaryBlue;
    opacity: 1;

    &:hover {
      opacity: .8;
    }
  }
  &.btn-light-blue {
    @include box-shadow(inset 0 0 0 2px $primaryBlue);
    background-color: $primaryBlue;
    opacity: 1;

    &:hover {
      opacity: .8;
    }
  }
  &.btn-pink {
    @include box-shadow(inset 0 0 0 2px $primaryMagenta);
    background-color: $primaryMagenta;
    opacity: 1;

    &:hover {
      background-color: transparent;
      color: $primaryMagenta;
    }
  }
  &.btn-green {
    @include box-shadow(inset 0 0 0 2px $supportGreen);
    background-color: $supportGreen;
    opacity: 1;

    &:hover {
      background-color: transparent;
      color: $supportGreen;
    }
  }
  &.btn-dark-blue {
    @include box-shadow(inset 0 0 0 2px $primaryNavy);
    background-color: $primaryNavy;
    opacity: 1;

    &:hover {
      background-color: transparent;
      color: $primaryNavy;
    }
  }
  &.btn-gray {
    @include box-shadow(none);
    background-color: rgba($layoutMediumBlack, 0.4);
    color: $layoutWhite;
  }
  &.btn-gray-full {
    @include box-shadow(inset 0 0 0 2px $layoutLightGrey);
    background-color: $layoutLightGrey;
    color: rgba($layoutMediumBlack, 0.4);
    opacity: 1;

    &:hover {
      background-color: transparent;
      color: $layoutLightGrey;
    }
  }
  &.btn-transparent {
    background-color: transparent;
    color: $layoutWhite !important;
    opacity: 1;

    &:hover {
      @include box-shadow(none);
      background-color: $primaryBlue;
      color: $layoutWhite;
    }

    &.btn-modal {
      padding-right: 10px;
      margin-top: 15px;
    }
  }
  &.btn-white-blue {
    @include box-shadow(inset 0 0 0 2px $primaryBlue);
    background-color: transparent;
    color: $primaryBlue;
    opacity: 1;

    &:hover {
      background-color: $primaryBlue;
      color: $layoutWhite;
    }
  }
  &.btn-white-dark-blue {
    @include box-shadow(inset 0 0 0 2px $primaryNavy);
    background-color: transparent;
    color: $primaryNavy;
    opacity: 1;

    &:hover {
      background-color: $primaryNavy;
      color: $layoutWhite;
    }
  }
  &.btn-white {
    @include box-shadow(inset 0 0 0 2px $layoutWhite);
    background-color: $layoutWhite;
    color: $layoutMediumBlack;
    opacity: 1;

    &:hover {
      background-color: transparent;
      color: $layoutWhite;
    }
  }
  &.btn-regular {
    @include transition(all 0.5s);
    @include box-shadow(none);
    background-color: $primaryBlue;
    color: $layoutWhite;
    width: 275px;
    opacity: 1;

    &:hover {
      opacity: .8;
    }
  }
  &.btn-isa {
    @include transition(all 0.5s);
    @include box-shadow(none);
    background-color: $primaryMagenta;
    color: $layoutWhite;
    width: 275px;
    opacity: 1;

    &:hover {
      opacity: .6;
    }
  }
}

.btn-edit {
  @include border-radius(14px);
  background-color: rgba($layoutWhite, 0.3);
  cursor: pointer;
  font-size: 1.3rem;
  padding: 4px 10px 2px;
  opacity: 0.5;

  &:hover {
    opacity: 1;
  }
}
.btn-add {
  @include border-radius(100%);
  background-color: rgba($layoutWhite, 0.2);
  color: $layoutWhite;
  cursor: pointer;
  display: inline-block;
  width: 30px;
  height: 30px;
  position: relative;
  opacity: 0.5;

  &:hover {
    opacity: 1;
  }
  &:before, &:after {
    @include transform(translate(-50%, -50%));
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: $layoutWhite;
  }
  &:before {
    width: 15px;
    height: 2px;
  }
  &:after {
    width: 2px;
    height: 15px;
  }
}

.btn-edit-text {
  @include transition(padding 0.2s);
  @include border-radius(6px);
  background-color: rgba($layoutWhite, 0.3);
  color: $layoutWhite;
  cursor: pointer;
  font-size: 1rem;
  opacity: 0.5;
  padding: 5px;
  text-decoration: none;

  &.loading {
    padding-left: 25px;
    position: relative;

    &:before {
      @include animation(loading 3s linear infinite);
      background-image: url(../images/icon-loader.png);
      background-size: 15px 15px;
      content: "";
      height: 15px;
      left: 6px;
      position: absolute;
      width: 15px;
      top: 5px;
    }
  }

  &:hover {
    opacity: 1;
  }
}


.btn-close {
  cursor: pointer;
  display: inline-block;
  height: 20px;
  opacity: 0.6;
  position: relative;
  text-decoration: none;
  width: 20px;

  &:hover {
    opacity: 1;
  }
  &:before, &:after {
    background-color: $layoutWhite;
    content: "";
    height: 2px;
    left: -2px;
    position: absolute;
    top: 9px;
    width: 23px;
  }
  &:before {
    @include transform(rotate(45deg));
  }
  &:after {
    @include transform(rotate(-45deg));
  }

}
// LOADING ANIMATION

@include keyframes(loading) {
  0% {
    @include transform(rotate(0deg));
  }
  100% {
    @include transform(rotate(360deg));
  }
}
