
.section-content{

  &:before {
    background-image: url(../images/bg-article.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center bottom;
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    z-index: -2;

    @include phone {
      height: 85%;
      margin-left: -50%;
      width: 200%;
    }

  }

  article {
    padding: 120px;
    @include phone {
      padding: 20px;
    }
  }
}