/*
  LIGHT MODAL
*/

.modal-light {
  color: $layoutMediumBlack;
  height: 100%;
  left: 0;
  overflow: auto;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99999;

  .bg-modal {
    background: rgba($layoutWhite, 0.95);
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
  }
  p {
    color: $layoutMediumBlack;
  }
  .title {
    color: $primaryMagenta;
    font-weight: 700;
    text-align: center;
  }
  .check-list {
    color: $layoutMediumBlack;
  }
  .content-box {
    background-color: $layoutWhite;
  }
  .btn-wrap {
    text-align: center;
  }
}
