/*
  RADIO
*/

.radio-yn {
  ul {
    margin: 0;
    padding: 0;

    li {
      display: inline-block;
      list-style: none;
      position: relative;

      &:first-child {
        margin-right: 10px;

        input[type="radio"]:checked {
          & + label {
            background-color: $supportGreen;
          }
        }
      }
      label {
        @include border-radius(6px);
        background-color: $layoutLightGrey;
        color: rgba($layoutMediumBlack, 0.2);
        display: block;
        font-size: 1.6rem;
        font-weight: bold;
        padding: 18px 20px;
        text-align: center;
        text-transform: uppercase;
        width: 40px;
      }
      input[type="radio"] {
        height: 100%;
        left: 0;
        opacity: 0;
        position: absolute;
        top: 0;
        width: 100%;

        &:checked {
          & + label {
            background-color: $primaryMagenta;
            color: $layoutWhite;
          }
        }
      }
    }
  }
}
