
.section-list {

  &:before {
    background-image: url(../images/bg-home-banner.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center bottom;
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    z-index: -2;

    @include phone {
      height: 85%;
      margin-left: -50%;
      width: 200%;
    }

  }

  article {
    padding: 60px;

    @include phone {
      padding: 10px;
    }
  }


  .blog-card {

    @include border-radius(4px);
    background-color: $layoutWhite;
    overflow: hidden;
    @include box-shadow(0 8px 24px 0 rgba($layoutBlack, 0.1));

    a{
      text-decoration: none;
    }

    .blog-card-image-link {
      display: block;
      overflow: hidden;
      border-radius: 5px 5px 0 0;
      position: relative;
    }

    .blog-card-image-large {
      width: auto;
      height: 500px;
      background: center center no-repeat $layoutLightGrey;
      background-size: cover;
    }

    .blog-card-image {
      width: auto;
      height: 200px;
      background: center center no-repeat $layoutLightGrey;
      background-size: cover;
    }

    .blog-card-content {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      justify-content: space-between;


      .blog-card-content-link {
        position: relative;
        flex-grow: 1;
        display: block;
        padding: 25px 25px 0;
        color: var(--darkgrey);

        .blog-card-header {

        }

        .blog-card-excerpt {

        }


      }

      .blog-card-meta{
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        padding: 0 25px 25px;

        .reading-time {
          flex-shrink: 0;
          margin-left: 20px;
          line-height: 33px;
          letter-spacing: .5px;
        }
      }


    }
  }


}