/*
  ERROR PAGES
*/

.simple-header {
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;

  .logo {
    background-image: url(../img/logo-dark-full.svg);
    background-repeat: no-repeat;
    display: inline-block;
    height: 37px;
    margin: 15px 0;
    width: 132px;

  }
}
.error-title {
  color: $primaryNavy;
  font-size: 4.8rem;
  font-weight: bold;
  line-height: 1.2;

  @include phone {
    font-size: 3rem;
  }
}
.page-error {
  min-height: 100vh;

  .content-area {
    margin: auto;
    max-width: 540px;
    position: relative;
    text-align: center;
    margin: 40px auto;
  }

  p {
    color: $primaryNavy;
    margin: 20px 0 35px;

    a {
      color: $primaryBlue;
      text-decoration: none;
      font-weight: 700;

      &:hover {
        color: $primaryMagenta;
      }
    }
  }
  .disclaimer p {
    color: rgba($layoutWhite, 0.6);
  }
  img {
    max-width: 100%;
  }
}
.page-404 {
  height: 100vh;

  @include respond-to-height(420px) {
    min-height: 0;
    height: auto;
  }
  .content-area {
    @include transform(translateY(-50%));
    top: 50%;
    margin: auto;

    @include respond-to-height(420px) {
      @include transform(none);
      top: 0;
      margin: 40px auto;
    }
  }
}
.page-maintenance {
  .content-area {
    margin-top: 80px;
  }
  img {
    @include phone {
      max-width: 70%;
    }
  }
}
