/*
  Footer
*/

.main-footer {
  position: relative;
  padding: 0 0 20px;
  overflow: hidden;
  background-color: $primaryNavy;

  .navigation {
    padding-bottom: 20px;

    @include respond-to(1000px) {
      padding-bottom: 40px;
    }

    @include phone {
      padding: 30px 10px 100px 10px;
      position: relative;
    }

    .get-started {
      padding: 12px 25px 10px;
    }
  }

  .disclaimer {
    border-top: 1px solid $layoutWhite;
    padding: 40px 20px;

    @include respond-to(1000px) {
      padding: 20px 40px 60px 40px;
    }

    @include phone {
      padding: 20px 10px 80px 10px;

      p:last-child {
        margin-top: 15px;
      }
    }
  }

  .footer-content {
    height: 240px;
    padding-left: 20px;
    position: relative;
    width: 30%;

    p {
      @include phone {
        float: left;
        margin: 0;
        padding-bottom: 30px;
      }
    }

    @include respond-to(1000px) {
      padding: 0 10px 0 20px;
      width: 28%;
    }

    @include phone {
      height: auto;
      position: initial;
      width: 45%;
    }
    @include phoneXS {
      width: 90%;
    }
  }

  .nav-items {
    .span2of12 {
      width: 14%;

      @include phone {
        width: 100%;
      }
    }
  }

  .bottom {
    bottom: 0;
    left: 0;
    padding-left: 20px;
    position: absolute;

    @include phone {
      left: 30px;
    }
  }

  .footer-info {
    p {
      color: $layoutWhite;
    }
    span {
      color: $layoutWhite;
    }

    img {
      margin-left: 5px;
      margin-bottom: -5px;
    }
    @include phoneXS {
      text-align: center;
    }
  }

  .item-content {
    color: $layoutWhite;
    margin-bottom: 30px;

    a {
      color: rgba($layoutWhite, 0.6);
      display: block;
      font-weight: 100;
      margin-top: 10px;
      text-decoration: none;
    }

    .logo {
      @include phone {
        margin-right: 100vw;
      }
    }

    .item-title {
      font-weight: 400;
      padding-bottom: 15px;
    }

    @include phone {
      float: left;
    }
  }

  .social {
    img {
      display: none;
    }
    p {
      display: block;
    }
  }

  .logo {
    width: 146px;
    height: 50px;
    display: block;
    float: left;

    @include phone {
      float: none;
      margin: auto;
    }
    img {
      max-width: 100%;
    }
  }
  p {
    color: rgba($layoutWhite, 0.6);
    font-weight: 100;

    @include phone {
      margin: auto 30px;
    }
  }
  .footer-nav {
    float: right;

    @include phone {
      float: none;
      text-align: center;
      ul {
        padding: 0;
      }
    }

    li {
      list-style: none;
      font-size: 1.4rem;
      font-weight: 100;
      display: inline-block;
      margin-left: 10px;

      @include phone {
        margin: 0 10px 10px;
      }
      a {
        color: $layoutWhite;
        text-decoration: none;
        text-transform: none;

        &:hover {
          color: $primaryMagenta;
        }
      }
    }
  }
  @include phone {
    .nav-items {
      display: inline-block;
      font-size: 1.6rem;
      margin-left: 20px;
      margin-top: 25px;
      width: 100%;

      @include phoneXS {
        @include transform(translateX(-50%));
        clear: both;
        display: block;
        left: 75%;
        margin-left: 0;
        position: relative;
        text-align: center;
      }

      .item-content {
        position: relative;
        float: left;
        width: 50%;

        @include phoneXS {
          left: -25%;
          width: 100%;
        }

        &.explore {
          @include phoneXS {
            float: none;
          }
        }

        &.members {
          margin-right: 0;
          @include phoneXS {
            float: none;
          }
        }

        &.social {
          clear: left;
          float: right;
          @include phoneXS {
            float: none;
          }

          a {
            display: inline;
          }

          img {
            display: inline;
            margin-right: 10px;
            width: 45px;
          }

          p {
            display: none;
          }
        }

        &.legal {
          float: left;
          margin: auto 0;
          @include phoneXS {
            float: none;
          }
        }
      }
    }
  }
}
