/*
  HEADER
*/

.main-header {
  @include clearfix;
  background-color: $layoutWhite;
  color: $layoutWhite;
  left: 0;
  padding-top: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;

  @include phone {
    padding-top: 10px;
  }

  .container {
    padding-left: 40px;

    @include menuBreak {
      padding: 20px 40px 0;
    }
    @include phone {
      padding: 0 20px;
    }
  }

  .nav-left {
    float: left;
    margin-top: 3px;

    @include respond-to(1130px) {
      display: none;
    }

    .nav-open & {
      @include respond-to(1130px) {
        display: block;
      }
    }

    ul {
      display: inline-flex;
      padding-left: 64px;

      @include respond-to(1130px) {
        @include transform(translateY(-50%));
        display: block;
        left: 58vw;
        padding-left: 0;
        position: relative;
        top: 18vh;
      }

      @include phone {
        @include transition(all 1s);
        left: 30vw;
        top: 95px;
      }

      li {
        list-style: none;
        margin-right: 15px;

        @include respond-to(1130px) {
          margin: 0;
        }

        a {
          color: $primaryNavy;
          text-decoration: none;
          font-size: 1.4rem;

          &:hover {
            color: $primaryBlue;
            span {
              color: $primaryBlue;
            }
          }

          p {
            padding-left: 21px;

            @include respond-to(1130px) {
              font-size: 1.6rem;
              margin-bottom: 24px;
              margin-top: 0px;
              padding-left: 0;
            }
          }

          &.personal {
            display: block;
            background: url(../img/menu-person.svg) 0 1px no-repeat;
            @include respond-to(1130px) {
              background: transparent;
            }
          }
          &.business {
            display: block;
            background: url(../img/menu-building.svg) 0 1px no-repeat;
            @include respond-to(1130px) {
              padding-left: 15px
            }
          }
          &.adviser {
            display: block;
            background: url(../img/menu-briefcase.svg) 0 1px no-repeat;
            @include respond-to(1130px) {
              padding-left: 15px;
            }
          }
        }
      }
    }
  }

  &.header {
    padding-top: 0;
  }

  &.light {
    background-color: $layoutWhite;

    .header-animated & {
      @include linear-gradient(90deg,#fff, #fff);
      @include box-shadow(0 0 10px 1px rgba(0,0,0, .1));

      .link-exit {
        margin: 30px 0;

        @include phone {
          margin: 17px 0;
        }
      }
    }
    .logo img {
      display: none;
    }
    .mobile-nav-btn {
      display: none;
    }
  }
  .social-nav {
    display: none;

    &.scale {
      background: $layoutWhite;
      bottom: 0;
      display: block;
      height: 60px;
      padding: 20px 0;
      position: fixed;
      text-align: center;
      width: 100%;
      z-index: 1000;

      a {
        margin-right: 15px;
      }
    }
  }
  .link-exit {
    @include transition(all 0.5s);
    color: $primaryNavy;
    float: right;
    margin: 42px 0;
    padding-right: 24px;
    position: relative;
    text-decoration: none;

    @include phoneSmall {
      margin: 18px 0;
      font-size: 0;
      height: 20px;
    }

    &:hover {
      color: $primaryMagenta;

      &:before, &:after {
        background-color: $primaryMagenta;
      }
    }
    &:before, &:after {
      background-color: $primaryNavy;
      content: "";
      height: 1px;
      width: 18px;
      right: 0;
      position: absolute;
      top: 10px;
    }
    &:before {
      @include transform(rotate(45deg));
    }
    &:after {
      @include transform(rotate(-45deg));
    }
  }

  .header-animated &  {
    @include box-shadow(0 0 10px 1px rgba(0,0,0, .05));
    background-color: $layoutWhite;

    @include respond-to(1130px) {
      @include box-shadow(none);
    }

    .logo {
      background-image: url(../images/logo-dark.png);
      width: 130px;
      height: 40px;
    }
  }
  .logo {
    @include transition(all 0.5s);
    background-image: url(../images/logo-dark.png);
    background-repeat: no-repeat;
    display: inline-block;
    float: left;
    height: 40px;
    margin: 20px 0 17px 0;
    width: 130px;

    @include respond-to(1130px) {
      margin: 10px 0;
      width: 124px;
      height: 36px;
    }

    &.dark {
      background-image: url(../images/logo-dark.png);
    }
    .nav-open & {
      display: none;
    }
    .page-home &, .page-contact &, .isa-corporate & {
      width: 164px;
      height: 46px;
      background-image: url(../images/logo-light.png);
    }
    .page-contact & {
      @include phone {
        background-image: url(../images/logo-dark.png);
      }
    }
    .header-animated & {
      background-image: url(../images/logo-dark.png);

      @include menuBreak {
        width: 115px;
        margin: 10px 0;
      }

    }

    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  .mobile-nav-btn {
    display: none;

    @include respond-to(1130px) {
      display: block;
      float: right;
      width: 30px;
      height: 20px;
      position: relative;
      margin: 16px 0;

      .nav-open & {
        position: fixed;
        top: 10px;
        right: 20px;
        z-index: 1;
      }

      &:before, &:after, span {
        @include transition(all 0.5s);
        background-color: $layoutMediumBlack;
        content: "";
        height: 1px;
        left: 0;
        position: absolute;
        width: 90%;

        .page-home &, .isa-corporate & {
          background-color: $layoutWhite;
        }
      }

      &:before, &:after, span {
        @include transition(all 0.5s);
        background-color: $layoutMediumBlack;
        content: "";
        height: 1px;
        left: 0;
        position: absolute;
        width: 90%;

        .page-home &, .isa-corporate & {
          background-color: $layoutWhite;
        }
      }
      &:before {
        top: 0;

        .nav-open & {
          @include transform(rotate(45deg));
          background-color: $layoutMediumBlack;
          top: 10px;
        }
        .header-animated & {
          background-color: $layoutMediumBlack;
        }
      }
      &:after {
        bottom: 10px;

        .nav-open & {
          @include transform(rotate(-45deg));
          background-color: $layoutMediumBlack;
          top: 10px;
        }
        .header-animated & {
          background-color: $layoutMediumBlack;
        }
      }
    }
  }
  .site-nav {
    @include transition(all 0.5s);
    float: right;
    margin-top: 17px;
    position: relative;

    @include respond-to(1130px) {
      background: transparent;
      display: none;
      position: fixed;
      width: 100%;
      height: 20%;
      top: 30vh;
      right: -63vw;
      margin: 0;

      .nav-open & {
        display: block;
      }
    }
    @include phone {
      height: 0;
      top: 0;
      right: -20%;
    }
    @include phoneSmall {
      right: 0;
    }
    .header-animated & {
      @include menuBreak {
        margin: 0;
      }
    }

    .open-account {
      @include box-shadow(none);
      background: $primaryBlue;
    }

    .menu-list {
      &.show {
        a {
          @include respond-to(1130px) {
            color: $primaryNavy;
            font-size: 1.4rem;
            margin-left: 0;
          }
        }
      }
    }

    ul {
      margin: 0;
      padding: 0;

      @include respond-to(1130px) {
        @include transform(translateY(-45%));
        position: relative;
        top: 20vh;
        height: 50vh;
        overflow: auto;
      }

      @include phone {
        @include transition(all 1s);
        left: 35vw;
        top: 55vh;
      }

      li {
        display: inline-block;
        list-style: none;

        @include respond-to(1130px) {
          display: block;
          margin: 30px 0;
        }

        @include phone {
          @include transition(all 1s);
          margin: 0 0 32px 0;
          text-align: left;
        }

        &.menu-button {
          @include phone {
            margin-bottom: 16px;
          }
        }

        a {
          &:hover {
            color: $primaryBlue;
            span {
              border-color: $primaryBlue;
            }
          }
        }

        &.risk-menu, &.about, &.operations, &.isa {

          a {
            display: block;
            padding-right: 25px;
            position: relative;

            @include respond-to(1130px) {
              display: inline-flex;
            }

            p {
              margin: 0;
            }
          }
        }
        .dropdown {
          @include transform(translateY(-45%) rotate(45deg));
          background-position: -155px -144px;
          border: solid $primaryNavy;
          border-width: 0 2px 2px 0;
          bottom: 0;
          content: '';
          display: block;
          height: 3px;
          margin: 0 0 3px 10px;
          width: 3px;
          padding: 3px;
          position: absolute;
          right: 0;
          top: 5px;

          @include respond-to(1130px) {
            border-color: $primaryNavy;
            top: 8px;
          }
        }
        .show {
          color: $primaryNavy;
          .dropdown {
            @include transform(rotate(225deg));
            border-color: $primaryNavy;
          }
        }

        a {
          color: $primaryNavy;
          font-size: 1.4rem;
          text-decoration: none;

          @include respond-to(1130px) {
            color: $layoutMediumBlack;
            font-size: 1.8rem;
          }

          p {
            @include respond-to(1130px) {
              font-size: 1.8rem;
            }
          }

          &.btn {
            &.login {
              color: $primaryNavy;
              background: url(../images/menu-lock-dark-blue.svg) 22px 15px no-repeat;
              padding: 0 24px 0 16px;

              @include respond-to(1130px) {
                background: url(../images/menu-lock-dark-blue.svg) 53px 17px no-repeat;
                width: 140px;
              }

              &:hover {
                color: $layoutWhite;
                background: $primaryNavy url(../img/menu-lock-white.svg) 22px 15px no-repeat;

                @include respond-to(1130px) {
                  background: $primaryNavy url(../img/menu-lock-white.svg) 53px 17px no-repeat;
                }
              }

              p {
                margin: 0;
                padding-left: 21px;
                padding-top: 14px;
                padding-bottom: 11px;
              }
            }
          }
          &.open-account {
            @include box-shadow(inset 0 0 0 2px rgba(255,255,255,.6));
            color: $layoutWhite;

            &:hover {
              color: $layoutWhite;
            }

            @include respond-to(1130px) {
              @include box-shadow(none);
              background: $primaryBlue;
              left: 0;
              padding: 0 20px;
              position: absolute;
              width: 140px;
            }

            p {
              margin: 0;
              @include respond-to(1130px) {
                padding-top: 14px;
                padding-bottom: 11px;
                margin: 0;
              }
            }
          }
        }
      }
    }
  }
}
.page-home, .page-contact, .isa-corporate {

  .nav-left {
    float: left;
    margin-top: 3px;

    @include respond-to(1130px) {
      display: none;
    }

    .nav-open & {
      @include respond-to(1130px) {
        display: block;
      }
    }

    ul {
      display: inline-flex;

      @include respond-to(1130px) {
        @include transform(translateY(-50%));
        display: block;
        left: 58vw;
        position: relative;
        top: 18vh;
      }

      @include phone {
        @include transition(all 1s);
        left: 30vw;
        top: 95px;
      }

      li {
        list-style: none;
        margin-right: 15px;

        @include respond-to(1130px) {
          margin: 0;
        }

        p {
          padding-left: 21px;

          @include respond-to(1130px) {
            color: $primaryNavy;
            font-size: 1.6rem;
            padding-left: 0;
          }
        }

        a {
          color: $layoutWhite;
          text-decoration: none;
          font-size: 1.4rem;

          &.personal {
            display: block;
            background: url(../img/menu-person-white.svg) 0 1px no-repeat;
            @include respond-to(1130px) {
              background: transparent;
            }
          }
          &.business {
            display: block;
            background: url(../img/menu-building-white.svg) 0 1px no-repeat;
            @include respond-to(1130px) {
              background: transparent;
            }
          }
          &.adviser {
            display: block;
            background: url(../img/menu-briefcase-white.svg) 0 1px no-repeat;
            @include respond-to(1130px) {
              background: transparent;
            }
          }
        }
      }
    }
  }

  .main-header {
    background-color: transparent;

    .site-nav {
      li {
        &.menu-button {
          @include phone {
            margin-bottom: 16px;
          }
        }
        a {
          color: $layoutWhite;

          &.btn {
            &.login {
              color: $primaryNavy;
              background: url(../images/menu-lock-dark-blue.svg) 22px 15px no-repeat;
              padding: 0 24px 0 16px;

              @include respond-to(1130px) {
                background: url(../images/menu-lock-dark-blue.svg) 53px 17px no-repeat;
                width: 140px;
              }

              &:hover {
                color: $layoutWhite;
                background: $primaryNavy url(../img/menu-lock-white.svg) 22px 15px no-repeat;

                @include respond-to(1130px) {
                  background: $primaryNavy url(../img/menu-lock-white.svg) 53px 17px no-repeat;
                }
              }
            }
            &.open-account {
              @include box-shadow(inset 0 0 0 2px rgba(255,255,255,.6));

              @include respond-to(1130px) {
                @include box-shadow(none);
              }
            }
          }
          &:hover {
            color: $primaryNavy;
          }

          @include respond-to(1130px) {
            color: $primaryNavy;
          }
          .dropdown {
            border-color: $layoutWhite;
            @include respond-to(1130px) {
              border-color: $primaryNavy;
              top: 8px;
            }
          }
        }
        .open-account {
          background-color: transparent;
          color: $layoutWhite;

          &:hover {
            @include box-shadow(none);
            background-color: $primaryBlue;
            color: $layoutWhite;
            opacity: 1;
          }

          @include respond-to(1130px) {
            @include box-shadow(none);
            background: $primaryBlue;
            left: 0;
            padding: 0 20px;
            position: absolute;
            width: 140px;
          }

          p {
            margin: 0;
            @include respond-to(1130px) {
              padding-top: 14px;
              padding-bottom: 11px;
              margin: 0;
            }
          }
        }
        &.risk {
          &:hover {
            a {
              color: $primaryBlue;
              span {
                border-color: $primaryBlue;
              }
            }
          }
          a {
            span {
              border-color: $layoutWhite;
              @include menuBreak {
                border-color: $primaryNavy;
              }
            }
            &.show {
              color: $layoutWhite;
              span {
                border-color: $layoutWhite;
              }
            }
          }
        }
      }
      .menu-list {
        &.show {
          a {
            @include respond-to(1130px) {
              color: $primaryNavy;
              font-size: 1.4rem;
              margin-left: 0;
            }
          }
        }
      }
    }
  }
  &.header-animated  {
    .main-header {
      @include box-shadow(0 0 10px 1px rgba(0,0,0, .05));
      background-color: $layoutWhite;

      @include respond-to(1130px) {
        @include box-shadow(none);
      }

      .logo {
        background-image: url(../images/logo-dark.png);
        height: 40px;
        width: 130px;
      }
      .nav-left {
        a{
          color: $primaryNavy;
          &.personal {
            display: block;
            background: url(../img/menu-person.svg) 0 1px no-repeat;
            @include respond-to(1130px) {
              background: transparent;
            }
          }
          &.business {
            display: block;
            background: url(../img/menu-building.svg) 0 1px no-repeat;
            @include respond-to(1130px) {
              background: transparent;
            }
          }
          &.adviser {
            display: block;
            background: url(../img/menu-briefcase.svg) 0 1px no-repeat;
            @include respond-to(1130px) {
              background: transparent;
            }
          }
        }
      }
      .site-nav {
        li {
          a {
            color: $primaryNavy;

            &.btn-white-blue {
              color: $primaryBlue;
              &:hover {
                color: $layoutWhite;
              }
            }
            &.btn-white-dark-blue {
              color: $primaryNavy;
              &:hover {
                color: $layoutWhite;
              }
            }
            .dropdown {
              border-color: $primaryNavy;
            }
          }
          .open-account {
            @include box-shadow(none);
            background-color: $primaryBlue;
            color: $layoutWhite;

            &:hover {
              opacity: .8;
            }
          }
          &.risk {
            a {
              span {
                border-color: $primaryNavy;
              }
            }
          }
        }
      }
    }
  }
}
.warning-msg {
  display: none;

  .container {
    @include respond-to(950px) {
      color: $primaryNavy;
      font-size: 1.4rem;
      padding-top: 0;
    }
  }

  @include respond-to(950px) {
    background-color: $layoutWhite;
    bottom: 0;
    color: $layoutMediumBlack;
    display: block;
    font-size: 1.6rem;
    font-weight: 100;
    left: 0;
    opacity: 0.9;
    padding: 20px 0;
    position: fixed;
    text-align: center;
    width: 100%;
    z-index: 500;

    a {
      font-weight: 400;
    }
  }
}