/*
  SELECT
*/

select {
  opacity: 0;
  position: absolute;
  z-index: -999999999999999;

  &[multiple], &.visible {
    opacity: 1;
    position: relative;
    z-index: 1;
  }
  &.visible {
    @include border-radius(0);
    -moz-appearance: none;
    -moz-user-select: -moz-none;
    -ms-user-select: none;
    -webkit-appearance: none;
    -webkit-user-select: none;
    appearance: none;
    background-clip: unset;
    background-color: transparent;
    border: none;
    color: $layoutWhite;
    opacity: 1;
    outline: none;
    padding: 20px;
    position: relative;
    user-select: none;
    width: 100%;
    z-index: 1;

    &:focus {
      border: 1px solid rgba(34, 34, 34, 0.6);
    }

    .form-marketing & {
      color: $layoutMediumBlack;
    }
  }
  option {
    color: $layoutMediumBlack;
  }
}
.select {
  width: 100%;
  border: 1px solid rgba(255,255,255,0.3);
  position: relative;

  .form-marketing & {
    border: 1px solid $layoutLightGrey;
  }
  .select-s & {
    @include border-radius(6px);
    border: 2px solid rgba(255,255,255,0.25);

    .selected-value {
      padding: 7px 20px;

      &:after {
        top: 13px;
        right: 10px;
      }
    }
  }
  .select-l & {
    .selected-value {
      padding: 25px 20px;
      font-family: $cohedline;
      font-size: 2.6rem;
      opacity: 0.5;
      text-align: center;
    }
  }
  .selected-value {
    padding: 18px 10px;
    position: relative;
    font-size: 1.4rem;

    &:after {
      @include transform(rotate(45deg));
      border-bottom: 2px solid $layoutWhite;
      border-right: 2px solid $layoutWhite;
      content: "";
      height: 6px;
      position: absolute;
      right: 18px;
      top: 25px;
      width: 6px;

      .form-marketing & {
        border-color: $layoutLightGrey;
      }
    }
  }
  &.select-open {
    z-index: 1;
    ul {
      display: block;
    }
  }
  ul {
    display: none;
    position: absolute;
    top: 100%;
    width: 100;
    top: 100%;
    padding: 0;
    margin: 0;
    background-color: $layoutWhite;
    left: -1px;
    width: 100%;
    border: 1px solid rgba(255,255,255,0.3);
    max-height: 180px;
    overflow-y: auto;

    .form-marketing & {
      border-color: $layoutLightGrey;
    }

    li {
      cursor: pointer;
      color: $layoutMediumBlack;
      list-style: none;
      padding: 10px 15px;

      &:hover {
        background-color: $primaryMagenta;
        color: $layoutWhite;
      }
    }
  }
}
.selectbox {
  border: 1px solid rgba($layoutWhite, 0.3);
  color: $layoutMediumBlack;
  position: relative;

  .form-marketing & {
    border: 1px solid rgba($layoutBlack, 0.1);
  }
  &.error {
    border-color: $supportRed;
  }
  .dob & {
    max-width: 140px;
    text-align: center;
    display: inline-block;
    margin-left: 10px;
    width: calc(33.333% - 10px);

    select {
      text-align: center;
    }
    &:first-child {
      margin-left: 0;
    }
  }
  &:after {
    @include transform(rotate(45deg));
    content: "";
    border-bottom: 2px solid $layoutWhite;
    border-right: 2px solid $layoutWhite;
    height: 10px;
    position: absolute;
    right: 20px;
    top: 22px;
    width: 10px;

    .form-marketing & {
      border-bottom: 2px solid rgba($layoutBlack, 0.1);
      border-right: 2px solid rgba($layoutBlack, 0.1);
    }
  }
}
