/*
  SLIDER CHECKBOX
*/

.checkbox-slider {
  display: inline-block;
  height: 30px;
  position: relative;
  width: 50px;

  input[type="checkbox"] {
    height: 100%;
    left: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;

    &:checked {
      & + label {
        background-color: $supportGreen;

        &:before {
          left: 22px;
        }
      }
    }
  }
  label {
    @include transition(all 0.5s);
    @include border-radius(30px);
    background-color: $layoutLightGrey;
    display: inline-block;
    height: 30px;
    width: 50px;

    &:before {
      @include transition(all 0.5s);
      @include border-radius(100%);
      @include box-shadow(0 0 2px 0 rgba(0,0,0,0.1));
      background-color: $layoutWhite;
      content: "";
      height: 28px;
      left: 0;
      position: absolute;
      top: 1px;
      width: 28px;
    }
  }
}
