/*
  UTIL
*/

// Media queries

@mixin desktopLarge {
  @media (min-width: #{$desktopLarge}) {
    @content;
  }
}
@mixin desktop {
  @media (min-width: $desktop) {
    @content;
  }
}
@mixin menuBreak {
  @media (max-width: #{$menuBreak}) {
    @content;
  }
}
@mixin tablet-only {
  @media (min-width: #{$tablet}) and (max-width: #{$tabletLandscape}) {
    @content;
  }
}
@mixin tablet {
  @media (max-width: #{$tabletLandscape}) {
    @content;
  }
}
@mixin tabletBig {
  @media (max-width: #{$tabletBig}) {
    @content;
  }
}
@mixin phone {
  @media (max-width: #{$phone}) {
    @content;
  }
}
@mixin phoneSmall {
  @media (max-width: #{$phoneSmall}) {
    @content;
  }
}
@mixin phoneXS {
  @media (max-width: #{$phoneXS}) {
    @content;
  }
}

@mixin respond-from($minWidth) {
  @media (min-width: #{$minWidth}) {
    @content;
  }
}
@mixin respond-to($maxWidth) {
  @media (max-width: $maxWidth) {
    @content;
  }
}
@mixin respond-to-from($minWidth, $maxWidth) {
  @media (min-width: #{$minWidth}) and (max-width: #{$maxWidth}) {
    @content;
  }
}
@mixin respond-to-height($maxHeight) {
  @media (max-height: $maxHeight) {
    @content;
  }
}

// CLEARFIX

@mixin clearfix {
  &:after {
    clear: both;
    content: "";
    display: block;
  }
}

// CSS 3

@mixin border-radius($radius) {
  -webkit-border-radius: $radius;
  border-radius: $radius;
  background-clip: padding-box;
}
@mixin transform($val) {
  -webkit-transform: $val;
  -moz-transform: $val;
  transform: $val;
}
@mixin transition($val) {
  -webkit-transition: $val;
  -moz-transition: $val;
  transition: $val;

}
// ANIMATION
@mixin keyframes($animation-name) {
  @-webkit-keyframes #{$animation-name} {
    @content;
  }
  @-moz-keyframes #{$animation-name} {
    @content;
  }
  @keyframes #{$animation-name} {
    @content;
  }
}

@mixin animation ($animation) {
  -webkit-animation: $animation;
  -moz-animation: $animation;
  -o-animation: $animation;
  animation: $animation;
}
@mixin box-shadow($shadow) {
  -webkit-box-shadow: $shadow;
  -moz-box-shadow: $shadow;
  box-shadow: $shadow;
}

// GRADIENT

@mixin linear-gradient($angle,$gradient1, $gradient2) {
  background-color: $gradient2; /* Fallback Color */
  background-image: -webkit-gradient(linear, $angle, from($gradient1), to($gradient2)); /* Saf4+, Chrome */
  background-image: -webkit-linear-gradient($angle, $gradient1, $gradient2); /* Chrome 10+, Saf5.1+, iOS 5+ */
  background-image:    -moz-linear-gradient($angle, $gradient1, $gradient2); /* FF3.6 */
  background-image:     -ms-linear-gradient($angle, $gradient1, $gradient2); /* IE10 */
  background-image:      -o-linear-gradient($angle, $gradient1, $gradient2); /* Opera 11.10+ */
  background-image:         linear-gradient($angle, $gradient1, $gradient2);
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=$angle,StartColorStr='#{$gradient1}', EndColorStr='#{$gradient2}');
}

// FLEXBOX

@mixin flex {
  display: -webkit-box;      /* OLD - iOS 6-, Safari 3.1-6 */
  display: -moz-box;         /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;      /* TWEENER - IE 10 */
  display: -webkit-flex;     /* NEW - Chrome */
  display: flex;
}

@mixin isaGradient {
  @include linear-gradient(45deg,#FF7E83, #EE5081);
}
@mixin purpleGradient($angle:45deg) {
  @include linear-gradient($angle, $gradientDrakPurple, $gradientLightPurple);
}
@mixin pinkGradient($angle:45deg) {
  @include linear-gradient($angle, $gradientPink, $gradientLightPink);
}
