.box {
  margin: 100px 0 0 8%;
  background: $layoutWhite;

  @include respond-to(950px) {
    display: none;
  }

  @include tablet {
    @include transform(scale(.85));
    margin-left: 0;
  };
}


.box-light {
  @include border-radius(6px);
  @include box-shadow(0 0 10px 1px rgba(0,0,0,0.05));
  padding: 20px;

  .item-icon {
    margin-top: -56px;
    width: 80px;
    height: 80px;
  }
  .text-l {
    color: $primaryMagenta;
    font-weight: 100;
    text-align: center;
    font-size: 2.6rem;
    max-width: 550px;
    margin: auto;

    @include phone {
      font-size: 2rem;
    }

    &.pink {
      line-height: 1.4;
      margin-top: 16px;
    }
  }

  &.floating-box {
    @include box-shadow(0 2px 8px 1px rgba($layoutDarkGrey, 0.2));
    @include transition(all .2s ease-in-out);
    height: 515px;
    position: relative;
    width: 455px;

    &:hover {
      @include box-shadow(0 8px 32px 1px rgba($layoutDarkGrey, 0.4));
      @include transform(scale(1.1));
      z-index: 2;
    }

    .styled-list {
      li {
        font-size: 1.6rem;
        line-height: 24px;
        margin: 30px 0;
        padding: 0 60px;

        &:after {
          position: absolute;
          left: 30px;
        }

        &:before {
          display: none;
          position: absolute;
        }
      }
    }
    .btn {
      bottom: 25px;
      margin: 0 15%;
      position: absolute;
    }
  }
}
