.pagination {
  list-style: none;
  li {
    display: inline;

    &.label {
      color: rgba($layoutWhite, 0.3);
      margin-left: 10px;
    }
    &.page-item {
      a {
        color: $layoutWhite;
        opacity: 0.3;
        margin-left: 10px;
        text-decoration: none;
        display: inline-block;
        padding: 6px;
        border-bottom: 3px solid transparent;
        text-transform: uppercase;
      }
    }
    &.active {
      a {
        border-bottom: 3px solid $layoutWhite;
        opacity: 1;
      }
    }
  }
}
