/*
  MODAL
*/

.modal {
  @include transition(opacity 0.5s);
  height: 100%;
  left: 0;
  opacity: 0;
  overflow-y: auto;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  display: none;

  .modal-open &, .page-investment & {
    display: block;
  }
  .modal-animate & {
    opacity: 1;
  }
  &.with-title {
    .modal-title {
      display: block;
    }
  }
  .consfimration-msg {
    padding-top: 180px;
  }
}
.scroll-content {
  height: 100%;
  overflow-y: scroll;
  position: relative;
  width: 100%;
}
.modal-bg {
  background-image: url(../img/bg/modal-bg.jpg);
  background-size: cover;
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;

  .modal-text & {
    background-image: none;
    background: rgba(0,0,0,0.8);
  }
}
.close-modal {
  color: $layoutWhite;
  font-size: 1.4rem;
  padding-right: 26px;
  position: absolute;
  right: 30px;
  text-decoration: none;
  top: 30px;
  z-index: 9999;

  @include tablet {
    position: fixed;
  }

  &:hover {
    cursor: pointer;
  }
}
.modal-header {
  height: 0;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;

  .container {
    position: relative;
  }
  .logo {
    background-image: url(../images/logo-light.png);
    display: inline-block;
    height: 38px;
    margin: 15px 0;
    width: 135px;
  }
}
.modal-content {
  @include transform(translateY(-50% ));
  color: $layoutWhite;
  margin: auto;
  max-height: 90%;
  max-width: 1100px;
  position: relative;
  top: 50%;

  &.small {
    max-width: 375px;
  }
  &.m-small {
    max-width: 560px;
  }
  &.medium {
    max-width: 600px;
  }
  &.large {
    max-width: 800px;
  }
  &.modal-video {
    max-height: 90%;
    max-width: 1080px;
  }
  .box-light {
    background-color: $layoutWhite;
    color: $layoutMediumBlack;

    &.padding {
      padding: 40px;
    }
  }
  .box-dark {
    background-color: #000;
    color: $layoutMediumBlack;

    &.padding {
      background-color: transparent;
      color: $layoutMediumBlack;
      margin: auto;
      max-width: 1080px;
      position: relative;
      width: 90vw;

      iframe {
        border: 50px solid #000;
        height: 315px;
        width: 560px;

        @include phone {
          @include transform(translate(-50%, -50%));
          @include transform(scale(.65));
          border: 50px solid #000;
          left: -50%;
          position: relative;
          top: 50%;
        }
      }
    }
  }
  input[type="submit"] {
    min-width: 200px;
    width: auto;
  }

  .bank-details {
    @include border-radius(6px);
    background-color: $layoutWhite;
    color: $primaryNavy;
    font-size: 1.8rem;
    margin: auto;
    max-width: 460px;
    padding: 100px 20px 40px 20px;
    position: relative;
    text-align: center;
    margin-bottom: 10px;

    .bank-info {
      margin-bottom: 8px;
      margin-top: 14px; 
      
      p {
        margin: 4px;
      }
    }

    .btn-blue {
      color: $primaryBlue;
      background-color: transparent;
      box-shadow: none;
      display: block;
      font-weight: 600;
      padding-top: 24px;

      &.close {
        position: relative;

        &:after {
          @include transform(rotate(45deg));
          @include border-radius(0);
          background-clip: padding-box;
          background: transparent;
          border-right: 2px solid $primaryBlue;
          border-bottom: 2px solid $primaryBlue;
          content: "";
          height: 8px;
          left: 38%;
          position: absolute;
          top: 26px;
          width: 4px;
        }
      }

      img {
        padding-right: 8px;
      }
    }

    .icon {
      &.bank {
        &:before {
          @include border-radius(100%);
          content: "";
          height: 80px;
          left: calc(50% - 60px);
          position: absolute;
          top: 40px;
          width: 120px;
          background-image: url('/static/img/bank-details.svg');
          background-position: center center;
          background-repeat: no-repeat;
          background-size: 46px auto;
        }
      }
    }
  }
}
.modal-title {
  color: $layoutWhite;
  display: none;
  font-family: $cohedline;
  font-size: 1.6rem;
  left: 20px;
  opacity: 0.5;
  position: fixed;
  text-transform: uppercase;
  top: 20px;
}
.confirmation-msg {
  @include border-radius(6px);
  background-color: rgba($primaryNavy, 0.4);
  color: $layoutWhite;
  margin-bottom: 40px;
  margin: auto;
  max-width: 460px;
  padding: 180px 20px 40px 20px;
  position: relative;
  text-align: center;
  margin-bottom: 10px;

  .box-transparent {
    @include border-radius(6px);
      background-color: transparent;
      color: #fff;
      text-align: center;
      max-width: 250px;
      padding: 10px 10px 0 10px;
      margin: 0 auto;
      border: 2px solid #fff;
  }

  &:before {
    @include border-radius(100%);
    content: "";
    height: 120px;
    left: 50%;
    margin-left: -60px;
    position: absolute;
    top: 40px;
    width: 120px;
  }
  &.email {
    p {
      line-height: 1.8;
      max-width: 380px;
      margin: 20px auto;
    }
    &:before {
      background-image: url(../img/envelope.svg);
      background-position: center center;
      background-repeat: no-repeat;
    }
  }
  &.pending {
    p {
      max-width: 300px;
      margin: 20px auto;
    }
    .bank-info {
      margin-bottom: 15px;

      p {
        margin: 0 0 5px 0;
        text-align: center;
      }
    }
    &:before {
      background-color: $supportOrange;
      background-image: url('/static/img/icons/icon-done.svg');
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 46px auto;
    }
  }
  &.success {
    &:before {
      background-color: $supportGreen;
    }
    &:after {
      @include transform(rotate(45deg));
      border-bottom: 6px solid $layoutWhite;
      border-right: 6px solid $layoutWhite;
      content: "";
      height: 30px;
      left: 50%;
      margin: 0 0 0 -10px;
      position: absolute;
      top: 74px;
      width: 15px;
      z-index: 1;
    }
  }
  &.warning {
    &:before {
      background: url(../img/icons/warning.svg) no-repeat center center;
      background-size: contain;
    }
  }
  .info-box {
    @include border-radius(6px);
    border: 1px solid rgba(255,255,255,0.3);
    font-weight: 100;
    margin: 40px auto 100px;
    max-width: 230px;
  }
  .btn {
    display: block;
  }
}
.add-card-modal {
  z-index: 1000;
}
.modal-heading {
  color: $primaryNavy;
  font-family: $cohedline;
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 40px;
  text-align: center;
}
