/*
  CHECKBOX
*/

// CARD CHECKBOX
.card-input {
  display: block;
  margin-bottom: 20px;
  position: relative;

  &:hover {
    label {
      opacity: 1;
    }
  }
  input[type="radio"] {
    height: 100%;
    opacity: 0;
    position: absolute;
    width: 100%;
    z-index: 1;

    &:checked + label {
      background-color: $layoutWhite;
      color: $primaryNavy;
      display: block;
      opacity: 1;

      &:before {
        background-color: $supportGreen;
        border-color: $supportGreen;
      }
      &:after {
        @include transform(rotate(45deg));
        content: "";
        position: absolute;
        width: 6px;
        height: 10px;
        border-bottom: 2px solid $layoutWhite;
        border-right: 2px solid $layoutWhite;
        left: -39px;
        top: 21px;
      }
      .icon {
        &.Visa {
          background-image: url(../img/icons/icon-visa-dark.svg);
        }
        &.MasterCard {
          background-image: url(../img/icons/icon-mastercard-dark.svg);
        }
      }
    }
  }
  label {
    @include border-radius(4px);
    border: 1px solid rgba($layoutWhite, 0.3);
    color: $layoutWhite;
    display: inline-block;
    font-weight: 100;
    line-height: 1.4;
    opacity: 0.4;
    padding-left: 55px;
    margin-left: 50px;
    position: relative;
    padding-top: 14px;
    padding-bottom: 14px;
    padding-left: 70px;

    &:before {
      @include border-radius(100%);
      content: "";
      width: 26px;
      height: 26px;
      border: 1px solid rgba($layoutWhite, 0.3);
      position: absolute;
      left: -50px;
      top: 15px;
    }
    .card-date {
      margin-right: 15px;
    }
    .icon {
      @include transform(translateY(-50%));
      @include border-radius(100%);
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 100% auto;
      background-size: 35px auto;
      height: 30px;
      left: 10px;
      position: absolute;
      top: 50%;
      width: 35px;


      &.Visa {
        background-image: url(../img/icons/icon-visa-white.svg);
      }
      &.MasterCard {
        background-image: url(../img/icons/icon-mastercard-white.svg);
      }
    }
  }
}
.checkbox {
  position: relative;

  input[type="checkbox"] {
    height: 100%;
    left: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    width: 100%;

    &.error {
      + label {

        &:before {
          border-color: $supportRed;
        }
      }
    }
    &:checked {
      + label {
        &:after {
          @include transform(rotate(45deg));
          content: "";
          border-bottom: 1px solid $supportGreen;
          border-right: 1px solid $supportGreen;
          height: 10px;
          left: 6px;
          position: absolute;
          top: 0;
          width: 6px;
        }
      }
    }
    &:focus {
      + label {
        &:before {
          border: 1px solid rgba(0,0,0,.3);
        }
      }
    }
  }

  label {
    padding-left: 30px;
    position: relative;

    a {
      color: inherit;

    }
    &:before {
      border: 1px solid rgba($layoutWhite, 0.3);
      bottom: 2px;
      content: "";
      height: 18px;
      left: 0;
      position: absolute;
      width: 18px;

      .form-marketing & {
        @include border-radius(4px);
        border: 1px solid rgba(0,0,0,.1);
      }
    }
  }
}

// SLIDER CHECKBOX

.slider-checkbox {
  display: inline-block;
  position: relative;

  input[type="checkbox"] {
    cursor: pointer;
    height: 100%;
    left: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    width: 100%;

    &:checked {
      + label {
        .slider-btn {
          background-color: $supportGreen;

          span {
            left: calc(100% - 22px)
          }
        }
      }
    }
    &:focus {
      + label {
        .slider-btn {
          @include box-shadow(0 0px 0px 1px rgba(0, 0, 0, 0.4))
        }
      }
    }
  }

  label {
    padding-left: 50px;
    display: block;

    .slider-btn {
      @include border-radius(25px);
      background-color: rgba($layoutMediumBlack, 0.3);
      display: inline-block;
      height: 22px;
      left: 0;
      position: absolute;
      top: 0;
      width: 34px;

      span {
        @include transition(all 0.5s);
        @include border-radius(100%);
        @include box-shadow(0 3px 3px 0 rgba(0,0,0,0.05));
        background-color: $layoutWhite;
        height: 22px;
        left: 0;
        position: absolute;
        top: 0;
        width: 22px;
      }
    }
  }
}
.row-hidden-fields {
  margin-bottom: 20px;

  &.active {
    position: relative;

    &:before {
      content: "";
      background-color: $supportGreen;
      height: 100%;
      left: -20px;
      position: absolute;
      width: 2px;
    }
  }
}
